import React, { Component } from 'react';
import $ from "jquery";

import "./css/pure.css"
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {lastTimestamp : 0, lastValue : 0, failed : false, busy : false, defaultSamples : 5, canSave : false};
    this.backendURL = 'http://localhost:9909'
  }

  performScan() {
    var params = {samples : Number(this.refs.samples.value)};
    this.setState({busy : true, canSave : false});
    $.ajax({
      timeout: 22000,
      url : this.backendURL + '/scan',
      method : 'GET',
      data : params,
      success : (data) => {
        this.setState({lastTimestamp : data.timestamp, lastValue : data.value.toFixed(2), failed : false, canSave : true})
      },
      error : (fail) => {
        console.log(fail);
        this.setState({lastTimestamp : Date.now(), lastValue : -1, failed : true});
      },
      complete : () => {
        this.setState({busy : false});
      }
    })
  }
  render() {
    return (
      <div className="App">
        <header>
          <button className="pure-button pure-button-primary" href="#" onClick={ (e) => this.performScan()} disabled={this.state.busy}>Scan</button>
          <label>Samples: <input type="number" min="1" max="25" ref="samples" value={this.state.defaultSamples} onChange={(e) => {this.setState({defaultSamples : e.target.value})}} /></label>
          <button className="pure-button" disabled={!this.state.canSave}>Save</button>
          <button className="pure-button" disabled={this.state.isBusy}>History</button>
        </header>
        <main>
          {this.state.busy && ( <div className="loading">LOADING</div>) }
          {!this.state.busy && !this.state.failed && this.state.lastTimestamp !== 0 && <div className="scan-result-window">{this.state.lastValue}</div> }
        </main>
      </div>
    );
  }
}

export default App;



